<template>
  <div class="mt-4">
      <v-card-title>
        <div class="text-h5 font-weight-light">
          Product
        </div>
        <v-spacer></v-spacer>
        <v-btn :to="{ name: 'AddProduct'}" color="primary" depressed elevation="2">
          <v-icon>mdi-plus</v-icon>
          Tambah</v-btn>
      </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table :headers="headers" :items="data.data" loading-text="Loading... Please wait" class="elevation-1" :loading="data.loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.isactive`]="{ item }">
          <v-chip small
            class="ma-2"
            :color="item.isactive ? 'success':'danger'"
            text-color="white"
          >
          {{ item.isactive ? 'Active' : 'Non Active' }}
          </v-chip>
        </template>
        <template #[`item.action`]="{ item }">
          <v-btn icon color="warning" @click="edit(item)">
            <v-icon small>
              mdi-lead-pencil
            </v-icon>
          </v-btn>
          <v-btn icon color="red" @click="hapus(item.id)">
            <v-icon small>
              mdi-delete
            </v-icon>
          </v-btn>
          <v-btn depressed small color="primary" :to="`product-detail/${item.ProductID}/${item.ProductName}`">
            RATE
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
export default {
  name: 'ProductPage',
  data: () => ({
    search: '',
    headers: [
      {
        text: 'ProductID',
        align: 'start',
        sortable: false,
        value: 'ProductID'
      },
      { text: 'Product Name', value: 'ProductName', sortable: false },
      { text: 'Description', value: 'ProductDescription', sortable: false, align: 'top' },
      { text: 'Financial Purpose', value: 'FinancePurpose', sortable: false },
      { text: 'Status', value: 'isactive', sortable: false, align: 'center' },
      { text: 'Action', value: 'action', sortable: false, width: '170px', align: 'center' }
    ]
  }),
  mounted: function () {
    store.dispatch('product/reqData')
  },
  computed: {
    ...mapState('product', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('product', [
      'edit', 'hapus', 'detail'
    ])
  }
}
</script>
